import React, { useState, useRef } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Progress } from '@/components/ui/progress';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Loader2, Download, Upload, ShieldCheck, AlertTriangle, CheckCircle2, FileArchive } from 'lucide-react';
import { exportBackup, importBackup } from '@/utils/backupHandler';
import { toast } from 'sonner';

import { Switch } from '@/components/ui/switch';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';

export const BackupManager: React.FC = () => {
  const [loading, setLoading] = useState(false);
  const [progress, setProgress] = useState(0);
  const [status, setStatus] = useState('');
  const [password, setPassword] = useState('');
  const [showPasswordDialog, setShowPasswordDialog] = useState(false);
  const [actionType, setActionType] = useState<'export' | 'import' | null>(null);
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  
  // Scheduling State
  const [autoBackupEnabled, setAutoBackupEnabled] = useState(false);
  const [backupFrequency, setBackupFrequency] = useState('daily');

  React.useEffect(() => {
    const enabled = localStorage.getItem('autoBackupEnabled') === 'true';
    const freq = localStorage.getItem('backupFrequency') || 'daily';
    setAutoBackupEnabled(enabled);
    setBackupFrequency(freq);
  }, []);

  const handleAutoBackupToggle = (enabled: boolean) => {
    setAutoBackupEnabled(enabled);
    localStorage.setItem('autoBackupEnabled', String(enabled));
    if (enabled) {
      toast.success('    ', { description: `    ${backupFrequency === 'daily' ? '' : ''}` });
    } else {
      toast.info('    ');
    }
  };

  const handleFrequencyChange = (value: string) => {
    setBackupFrequency(value);
    localStorage.setItem('backupFrequency', value);
  };

  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleExport = async () => {
    try {
      setLoading(true);
      setShowPasswordDialog(false); // Close dialog to show progress in card
      setStatus('   ...');
      setProgress(0);

      const result = await exportBackup(password || undefined, (status, prog) => {
        setStatus(status);
        setProgress(prog);
      });

      if (result.success) {
        toast.success('    ', {
          description: result.message,
        });
        setStatus(`  : ${result.stats?.records || 0} `);
      } else {
        toast.error('  ', {
          description: result.message,
        });
        setStatus('  ');
      }
    } catch (error: any) {
      toast.error('', { description: error.message });
    } finally {
      setLoading(false);
      setPassword('');
    }
  };

  const handleImport = async () => {
    if (!selectedFile) return;

    try {
      setLoading(true);
      setShowPasswordDialog(false); // Close dialog to show progress in card
      setStatus('  ...');
      setProgress(0);

      // Optional: Ask for confirmation before restore
      // (This is implicitly handled by the dialog flow)

      const result = await importBackup(selectedFile, password || undefined, (status, prog) => {
        setStatus(status);
        setProgress(prog);
      });

      if (result.success) {
        toast.success('  ', {
          description: `  ${result.stats?.records || 0} .`,
        });
        setStatus(' ');
        // Reload page to reflect changes
        setTimeout(() => window.location.reload(), 2000);
      } else {
        toast.error(' ', {
          description: result.message,
        });
        setStatus(' ');
      }
    } catch (error: any) {
      toast.error('', { description: error.message });
    } finally {
      setLoading(false);
      setPassword('');
      setSelectedFile(null);
    }
  };

  const initiateAction = (type: 'export' | 'import') => {
    setActionType(type);
    setPassword('');
    setStatus('');
    setProgress(0);
    
    if (type === 'import') {
      fileInputRef.current?.click();
    } else {
      setShowPasswordDialog(true);
    }
  };

  const onFileSelected = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      setSelectedFile(e.target.files[0]);
      setShowPasswordDialog(true);
    }
  };

  return (
    <Card className="w-full" dir="rtl">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <ShieldCheck className="h-5 w-5 text-primary" />
            
        </CardTitle>
        <CardDescription>
              .         .
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div className="flex flex-col sm:flex-row gap-4">
          <Button 
            variant="outline" 
            className="flex-1 h-24 flex-col gap-2" 
            onClick={() => initiateAction('export')}
            disabled={loading}
          >
            <Download className="h-8 w-8 mb-1" />
            <span className="font-semibold">  </span>
            <span className="text-xs text-muted-foreground">   </span>
          </Button>
          
          <Button 
            variant="outline" 
            className="flex-1 h-24 flex-col gap-2"
            onClick={() => initiateAction('import')}
            disabled={loading}
          >
            <Upload className="h-8 w-8 mb-1" />
            <span className="font-semibold">  </span>
            <span className="text-xs text-muted-foreground">  </span>
          </Button>
        </div>

        <input 
          type="file" 
          ref={fileInputRef} 
          className="hidden" 
          accept=".zip,.json,.enc" 
          onChange={onFileSelected} 
        />

        {loading && (
          <div className="space-y-2 py-4">
            <div className="flex justify-between text-sm">
              <span>{status}</span>
              <span>{progress}%</span>
            </div>
            <Progress value={progress} className="h-2" />
          </div>
        )}

        <div className="flex items-center justify-between p-4 border rounded-lg bg-muted/20">
          <div className="space-y-0.5">
            <Label className="text-base">  </Label>
            <p className="text-sm text-muted-foreground">     .</p>
          </div>
          <div className="flex items-center gap-4">
            <Select value={backupFrequency} onValueChange={handleFrequencyChange} disabled={!autoBackupEnabled}>
              <SelectTrigger className="w-[120px]">
                <SelectValue placeholder="" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="daily"></SelectItem>
                <SelectItem value="weekly"></SelectItem>
              </SelectContent>
            </Select>
            <Switch 
              checked={autoBackupEnabled} 
              onCheckedChange={handleAutoBackupToggle} 
            />
          </div>
        </div>

        {!loading && status && (
          <Alert variant={status.includes('') || status.includes('failed') ? "destructive" : "default"}>
            {status.includes('') || status.includes('failed') ? <AlertTriangle className="h-4 w-4" /> : <CheckCircle2 className="h-4 w-4" />}
            <AlertTitle>{status.includes('') || status.includes('failed') ? "" : ""}</AlertTitle>
            <AlertDescription>{status}</AlertDescription>
          </Alert>
        )}
      </CardContent>

      <Dialog open={showPasswordDialog} onOpenChange={setShowPasswordDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>
              {actionType === 'export' ? '  ' : '  '}
            </DialogTitle>
            <DialogDescription>
              {actionType === 'export' 
                ? '        .    .' 
                : '      .       .'}
            </DialogDescription>
          </DialogHeader>
          
          {actionType === 'import' && selectedFile && (
            <div className="flex items-center gap-2 p-2 bg-muted rounded-md text-sm">
              <FileArchive className="h-4 w-4" />
              <span className="truncate">{selectedFile.name}</span>
              <span className="mr-auto text-xs text-muted-foreground">
                {(selectedFile.size / 1024 / 1024).toFixed(2)} MB
              </span>
            </div>
          )}

          <div className="space-y-2 py-2">
            <Label htmlFor="backup-password">  ()</Label>
            <Input
              id="backup-password"
              type="password"
              placeholder="  ..."
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
          </div>

          <DialogFooter className="flex gap-2 justify-end sm:justify-start">
            <Button variant="ghost" onClick={() => setShowPasswordDialog(false)}></Button>
            <Button 
              onClick={actionType === 'export' ? handleExport : handleImport}
              disabled={loading}
              className={actionType === 'import' ? 'bg-green-600 hover:bg-green-700' : ''}
            >
              {loading ? <Loader2 className="ml-2 h-4 w-4 animate-spin" /> : null}
              {actionType === 'export' ? ' ' : ' '}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </Card>
  );
};
