import { useEffect } from 'react';
import { exportBackup } from '@/utils/backupHandler';
import { toast } from 'sonner';

export const useScheduledBackup = () => {
  useEffect(() => {
    const checkAndRunBackup = async () => {
      try {
        const enabled = localStorage.getItem('autoBackupEnabled') === 'true';
        if (!enabled) return;

        const frequency = localStorage.getItem('backupFrequency') || 'daily';
        const lastBackupStr = localStorage.getItem('lastAutoBackupTime');
        const lastBackup = lastBackupStr ? new Date(lastBackupStr).getTime() : 0;
        const now = Date.now();
        
        const oneDay = 24 * 60 * 60 * 1000;
        const interval = frequency === 'weekly' ? 7 * oneDay : oneDay;

        if (now - lastBackup > interval) {
          console.log('Running scheduled backup...');
          // Run backup without password for automated runs (or use a stored key if implemented)
          // We run it silently, but show a toast on completion
          const result = await exportBackup(undefined, (status, progress) => {
             // Optional: Update a global state for progress if needed
             console.log(`Auto Backup: ${status} ${progress}%`);
          });

          if (result.success) {
            localStorage.setItem('lastAutoBackupTime', new Date().toISOString());
            toast.success('    ', {
              description: result.message
            });
          } else {
            console.error('Scheduled backup failed:', result.message);
            toast.error('   ', {
              description: result.message
            });
          }
        }
      } catch (error) {
        console.error('Error in scheduled backup:', error);
      }
    };

    // Check immediately on mount
    checkAndRunBackup();

    // And check periodically (e.g., every hour) in case the app stays open
    const timer = setInterval(checkAndRunBackup, 60 * 60 * 1000);

    return () => clearInterval(timer);
  }, []);
};
